.ts_rt_Post_hover {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0;
    z-index: 1;
    transition: .2s linear;
    -moz-transition: .2s linear;
}

.ts_rt_card_content_00 {
    padding: 5%;
}

.ts_rt_card_hover_content {
    position: absolute;
    bottom: 50%;
    left: 50%;
    right: auto;
    text-align: center;
    width: 100%;
    padding: 10px;
    -webkit-transform: translate(-50%,50%);
    transform: translate(-50%,50%);
}

.ts_rt_card_content{
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-align: center;
    line-height: 1.5;
}

.ts_rt_card_hover_overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background-color: #fff;
    opacity: .8;
    transition: background 0.5s, color 0.5s;
    -moz-transition: background 0.5s, color 0.5s;
    -ms-transition: background 0.5s, color 0.5s;
    -o-transition: background 0.5s, color 0.5s;
    -webkit-transition: background 0.5s, color 0.5s;
}

.ts_rt_Post_hover:hover{
    opacity: 1;
}

.ts_rt_slider-social-icon {
    // font-size: 40px;
    margin-bottom: 10px;
}

.ts_rt_shop_btn {
    border-radius: 0px;
