.tb_ms_slider_next_arrow-next, 
.tb_ms_slider_pre_arrow-prev {
    width: 40px;
    height: 70px;
    overflow: hidden;
    top: 50%;
    // margin-top: -27px;
    transform: translate(0, -50%);
    position: absolute;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.tb_ms_slider_pre_arrow-prev.splide__arrow--prev{
    left: 7px;
    justify-content: start;
}

.tb_ms_slider_next_arrow-next.splide__arrow--next{
    right: 7px;
    justify-content: end;
}

.tb_ms_slider_pre_arrow-prev.splide__arrow--prev .splideArrow_inner{
    border-radius: 0px 3px 3px 0px;
    -webkit-border-radius: 0px 3px 3px 0px;
    -moz-border-radius: 0px 3px 3px 0px;
    padding: 10px 10px 10px 6px;
}

.tb_ms_slider_next_arrow-next.splide__arrow--next .splideArrow_inner {
    border-radius: 3px 0px 0px 3px;
    -webkit-border-radius: 3px 0px 0px 3px;
    -moz-border-radius: 3px 0px 0px 3px;
    padding: 10px 6px 10px 10px;
}

.tb_ms_slider_next_arrow-next .splideArrow_inner, 
.tb_ms_slider_pre_arrow-prev .splideArrow_inner {
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    background-color: #fff;
    opacity: .8;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.6);
    border: 0;
    color: #000;
    font-size: 14px;
}

.ts_rt_card_socialAction > a:hover > div {
