.ts_reelTheme_spinner {
    position: absolute;
    z-index:9;
}

.ts_reelTheme_spinner:before {
    content: '';
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 0;
    border-radius: 50%;
    border: 2px solid #d1d3e0;
    border-right: 2px solid transparent;
}

.ts_reelTheme_spinner:before {
    width: 1.5rem;
    height: 1.5rem;
    margin-top: -0.75rem;
}

.ts_reelTheme_spinner:before {
    -webkit-animation: animation-spinner .5s linear infinite;
    animation: animation-spinner .5s linear infinite;
}

.ts_reelTheme_spinner.ts_reelTheme_spinner-white:before {
    border: 2px solid #fff;
    border-right: 2px solid transparent;
}

.ts_reelTheme_spinner.ts_reelTheme_spinner-right:before {
    left: auto;
    right: 1rem;
}

.ts_reelTheme_spinner.ts_reelTheme_spinner-right.btn:not(.btn-block) {
    padding-right: 3.5rem;
}

@-webkit-keyframes animation-spinner{
    to{
        -webkit-transform:rotate(360deg);
        transform:rotate(360deg)
    }
}
@keyframes animation-spinner{
    to{
        -webkit-transform:rotate(360deg);
        transform:rotate(360deg)
    }
}


.ts_reelTheme_overlay_loader {
    position: absolute;
