.ts_addToCart_modal{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    outline: 0;
    display: block;
    opacity: 1;
    visibility: visible;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: rgba(0,0,0,0.5);
    transition: opacity 0.15s linear;
}

.ts_addToCart_modalDialog{
    position: relative;
    width: auto;
    margin: 0.5rem;
    pointer-events: none;
    transform: translate(0, 0);
    min-height: calc(100% - 3.5rem);
    display: flex;
    align-items: center;
}



@media (min-width: 576px){
    .ts_addToCart_modalDialog {
        max-width: 500px;
        margin: 1.75rem auto;
    }

}

@media (min-width: 768px){
    .ts_addToCart_modalDialog {
        max-width: 750px;
    }
}

.ts_addToCart_modalContent {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border-radius: 4px;
    outline: 0;
}

.ts_addToCart_modalBody {
    position: relative;
    padding: 20px 0px 38px 0px;
}

.ts_addToCart_modalBody_inner{
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    display: flex;
}

.ts_addToCart_backButton {
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left: 26px;
    margin-bottom: 9px;
    line-height: 20px;
    padding: 5px 10px 5px 0;
    cursor: pointer;
    font-family: inherit;
}

.ts_addToCart_backButton svg{
    margin-right: 10px;
}

.ts_addToCart_productImage {
    width: 300px;
    padding: 0 26px;
}

.ts_addToCart_productImage img{
    width: 100%;
    height: auto;
}

.ts_addToCart_productDetail {
    width: calc(100% - 300px);
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    padding: 0 40px 0 20px;
}

.ts_addToCart_productTitle {
    font-size: 20px;
    color: #000;
    line-height: 30px;
    font-weight: 400;
    margin-bottom: 4px;
    font-family: inherit;
    word-break: break-all;
}

.ts_addToCart_productPrice {
    font-size: 18px;
    line-height: 27px;
    font-weight: 600;
    font-family: inherit;
}

.ts_addToCart_colorPalate_section {
    margin-top: 21px;
}

.ts_addToCart_colorHeading {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 8px;
    font-family: inherit;
}

.ts_addToCart_color {
    width: 38px;
    height: 38px;
    border: 1px solid #DEDEDE;
    display: inline-flex;
    margin-right: 8px;
    margin-bottom: 10px;
    padding: 4px;
}

.ts_addToCart_colorTile {
    width: 100%;
    height: 100%;
}

.ts_addToCart_sizeChart {
    display: flex;
    flex-wrap: wrap;
    margin-top: 14px;
}

.ts_addToCart_sizeChart_label {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 7px;
    width: 100%;
    font-family: inherit;
}

.ts_addToCart_sizebutton {
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    display: inline-block;
    padding: 10px;
    border: 1px solid #DEDEDE;
    margin-right: 8px;
    margin-bottom: 10px;
    width: 80px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-family: inherit;
    transition: all 0.3s ease-in-out;
}

.ts_addToCart_sizebutton:last-child {
    margin-right: 0;
}

.ts_addToCart_sizebutton:hover,
.ts_addToCart_sizebutton:active,
.ts_addToCart_sizebutton.active{
    border-color: #000;
}

.ts_productSize_unvavailable {
    position: relative;
    background: #DEDEDE;
    border-color: #DEDEDE;
    cursor: no-drop;
    pointer-events: none;
}

.ts_productSize_unvavailable_cross{
    position: absolute;
    top: 49%;
    left: -6px;
    width: 115%;
    height: 1px;
    background-color: #000;
    -webkit-transform: rotate(-28deg);
    transform: rotate(-28deg);
}

.ts_productSize_unvavailable:hover{
    border-color: #DEDEDE ;
}

.ts_addToCart_actions {
    display: flex;
    justify-content: space-between;
    margin-top: 21px;
}

.ts_addToCart_actions_cancel {
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    padding: 10px 40px;
    border-radius: 4px;
    border: 1px solid #000;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    font-family: inherit;
    transition: all 0.3s ease-in-out;
    box-shadow: 0 0 3px rgba(0,0,0,0); 
}

.ts_addToCart_actions_cancel:hover{
    box-shadow: 0 0 3px rgba(0,0,0,0.6);  
}

.ts_addToCart_actions_addCart {
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    padding: 10px 40px;
    border-radius: 4px;
    border: 1px solid #000;
    background: #000;
    color: #fff;
    width: calc(100% - 13px);
    margin-left: 13px;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    font-family: inherit;
    transition: all 0.3s ease-in-out;
}

.ts_addToCart_actions_addCart:hover, 
.ts_addToCart_actions_addCart:focus{
    background: transparent;
    color:#000;
    box-shadow: 0 0 3px rgba(0,0,0,0.6); 
}

/*=== CHECKOUT MODAL CSS START ===*/

.ts_checkout_modal .ts_addToCart_modalBody {
    padding-bottom: 30px;
}

.ts_checkout_modal .ts_addToCart_productDetail {
    width: calc(100% - 420px);
}

.ts_checkout_modal .ts_addToCart_productTitle {
    word-break: break-word;
    font-family: inherit;
}

.ts_checkout_modal .ts_addToCart_actions{
    flex-wrap: wrap;
}

.ts_checkout_modal  .ts_addToCart_actions_addCart, 
.ts_checkout_modal  .ts_addToCart_actions_cancel {
    width: 100%;
}

.ts_checkout_modal .ts_addToCart_productDetail {
    width: 100%;
}

.ts_checkout_modal .ts_addToCart_actions {
    display: flex;
    justify-content: space-between;
    margin-top: 21px;
}

.ts_addToCart_actions_checkOut {
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    padding: 10px 40px;
    border-radius: 4px;
    border: 1px solid #000;
    background: #000;
    color: #fff;
    width: calc(100% - 13px);
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-bottom: 13px;
    font-family: inherit;
    transition: all 0.3s ease-in-out;
}

.ts_addToCart_actions_checkOut:hover, 
.ts_addToCart_actions_checkOut:focus{
    background: transparent;
    color:#000;
    box-shadow: 0 0 3px rgba(0,0,0,0.6); 
}

.ts_addToCart_actions_shopping {
    font-size: 14px;
    font-weight: 600;
    line-height: 21px;
    padding: 10px 40px;
    border-radius: 4px;
    border: 1px solid #000;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    font-family: inherit;
    transition: all 0.3s ease-in-out;
    box-shadow: 0 0 3px rgba(0,0,0,0); 
}

.ts_addToCart_actions_shopping:hover{
    box-shadow: 0 0 3px rgba(0,0,0,0.6);  
}

.ts_addToCart_product_added {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 39px;
    color: #8F8F8F;
    margin-top: 12px;
    font-family: inherit;
}

.ts_checkout_modal .ts_addToCart_actions{
     margin-top: 0;
}

.ts_addToCart_color.active {
    border-color: #000;
}

.ts_addToCart_selectOptionsError{
    background-color: #fcc;
    padding: 8px 15px;
    color: black;
    opacity: 1;
    transition: all 0.25s;
}

@media (min-width: 768px){
    .ts_checkout_modal .ts_addToCart_modalDialog {
        max-width: 560px;
    }

}


/*=== CHECKOUT MODAL CSS END ===*/


@media screen and (max-width:767px){

    .ts_addToCart_modalBody_inner{
        flex-wrap: wrap;
    }

    .ts_addToCart_productImage {
        padding: 0 15px;
        width: 100%;
    }

    .ts_addToCart_productDetail {
        width: 100%;
        padding: 20px 15px 0;
    }
}

@media screen and (max-width:575px){
    .ts_addToCart_sizebutton {
        margin-bottom: 15px;
        margin-right: 15px;
    }

    .ts_addToCart_color{
        margin-right: 14px;
    }

    .ts_addToCart_backButton {
        margin-left: 15px;
    }

    .ts_addToCart_actions_addCart {
        margin-left: 25px;
        width: calc(100% - 25px);
    }

}

@media screen and (max-width:389px){
    .ts_addToCart_actions {
        flex-wrap: wrap;
    }

    .ts_addToCart_actions_cancel {
        width: 100%;
    }

    .ts_addToCart_actions_addCart {
        margin-left: 0;
        width: 100%;
        margin-top: 20px;
    }

    
}